@import '../../scss/index.scss ';

.forms{
    background-color: $white;
    border:1px solid $purple;
    height: 70vh;
    margin:0;
    padding:0;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    
    .form-design{
        width: 30vw;
        height: auto;
        border:1px solid $purple;
        text-align: center;
        background-color: $smoke;
        border: 1px solid $purple;
        border-radius: 20px;
        display: flex;
        justify-content: center;
        align-items:center;
        flex-direction: column;

        .form-design-heading{
            color:$purple;
        }

        .Fieldtext{
            
            margin-top: 15px;
            width: 70%;
            background: transparent;
            border :none;
            font-size: $font-size-light;
            
            
        }
        .Fieldtext:focus-visible{
            width: 70%;
            background: transparent;
            border :none;
            outline:none;
            border-bottom: 2px solid $purple;
        }
        .Fieldradio{
            display: flex;
            margin-top: 15px;
            width: 70%;
            flex-direction: column;
            align-items: flex-start;
            
        }

        .Fieldradio:hover{
            border:solid $purple;
            border-radius: 2px ;
        }

        .form-button{
            margin: 5px 0px 5px 0px;

            width: 50px;
            height: 30px;
            background-color: $purple;
            border:$purple 5px;
            border-radius:  12px;
            opacity: 0.7;
            color: $white;
            transition: width 1.7s;
            
        }

        .form-button:hover, .form-button:focus{
            opacity: 1;
            width: 70px;
        }

    }

}