@import '../../scss/index.scss';



.container{

    margin :10px;
    box-sizing: border-box;

    .card{
        display:grid;
        max-width: 200rem;
        grid-template-columns: 1fr 1fr 1fr;
        gap : 1.6rem;

        .genshin {
            display:grid;
            border:1px solid $purple;
            gap:1.6rem;
        
          .card-image{
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
    }
}

@media only screen and (max-width:768px) and (min-width:0px){

    .container{

        margin: 10px;
        .card{
            grid-template-columns: 340px;

            .genshin{
                width: 340px;
                padding:10%;
                color:$purple;
                
            }
        }
    }
    
}
